.App-wrapper{
  display: grid;
  grid-template-areas:
  "header header"
  "nav main";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap:10px;
  background: #00f4f5;
}

.App-wrapper-content{
  grid-area: main;
  background-color: cyan;
  min-height: 784px;
  border-radius: 5px;
}

