*{
    box-sizing: border-box
}


.ava{
    width: 150px;
    height: 150px;
    border-radius: 5%;
}

.preloader  img {
position: absolute;
    width: 60%;
    height: 60%;
    z-index: 3;
    opacity: 0.3;
}
.PageName{
    text-align: center;
    font-family: Gadugi ;
    color: blue;
    border-color: #717173;
    border: double ;
    border-radius: 10px;
}

.allUsers{
    margin-left: 10px;
    margin-top:10px
}
.oneUser{
    border: solid 2px darkblue;
    margin-bottom: 10px;
    border-radius: 5px;
}
.oneUser >div{
    padding-right: 85%;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-top: 10px;
    border-radius: 2px;
}
.oneUser >div {
    margin-top: 10px;
    border-bottom: double 1px blueviolet;

}
.paginator{
    font-size: 20px;
}


.oneUser :hover{
 background-color: #00020310;
}
.PageName:hover{
    background-color: #00020310;

}

