.conteiner{
    width: 100%;
    margin: 30px auto;
    overflow: hidden;
}
.conteiner ul li{
    list-style: none;
    float: left;
    font:10px/1.5 Verdana ,Helvetica, SansSerif;
    width:16%;
    margin: 2% 2% 10px 2%;
}
.conteiner ul li a {
    color: #ff3528
}

.conteiner ul div li a img {
    /*щоб не заповзали картинки одна на одну  а відображалось всі 100% від верхнього
 хоч і менш розмір*/
    max-width: 100%;
}
@media only screen and (max-width: 1420px),
only screen and (max-device-width: 1420px){
    .conteiner ul li  {
        width: 21%;
        color: #000;
    }
}
@media only screen and (max-width: 720px),
only screen and (max-device-width: 720px){
    .conteiner ul li  {
        width: 29%;
        color: #000;
    }
}
@media only screen and (max-width: 520px),
only screen and (max-device-width: 520px){
    .conteiner ul li  {
        width: 46%;
        color: #000;
    }
}
@media only screen and (max-width: 320px),
only screen and (max-device-width: 420px){
    .conteiner ul li  {
        width: 96%;
        color: #000;
    }
}
