* {
    box-sizing: border-box;
}


.gallery {
    width: 100%;
}

.gallery div li {
    list-style: none;
}

.gallery div li a img {
    overflow: hidden;
}

.gallery div li {
    margin: 1% 2% 3% 2%;
    max-width: 20%;
    float: left;
}

.gallery div li a img {
    width: 100%;
}

@media only screen and (max-width: 1420px) {
    .gallery div li {
        max-width: 16%;
        color: #008800;
    }
}
@media only screen and (max-width: 1300px) {
    .gallery div li {
        max-width: 21%;
        color: #008800;
    }
}

@media only screen and (max-width: 1100px) {
    .gallery div li {
        max-width: 29.333%;
        color: #008800;
    }
}
@media only screen and (max-width: 800px) {
    .gallery div li {
        max-width: 46%;
        color: #008800;
    }
}
@media only screen and (max-width: 500px) {
    .gallery div li {
        max-width: 96%;
        color: #008800;
    }
}


.animate{
    border: black;

    width: 100%;
    height:100px;
    background-repeat: no-repeat;
    /*адаптація під ширину і висоту*/
    background-size: cover;
    overflow: hidden;
    animation: ananimate 10s infinite ease ;
}


@keyframes ananimate {
    from, to,
    20%{
        background-image: url("../../acets/images/Foto/па1.jpg");
    }
    20%, 66% {
        background-image: url("../../acets/images/Foto/пано2.jpg");
    }
    66%, 99% {
        background-image: url("../../acets/images/Foto/па3.jpg");

    }
}





.conteiner{
    width: 100%;
    position:relative;
    margin: 0 2%;
}
.prev, .next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -22px;
    color: white;
    font-weight: bold;
    font-size: 25px;
    background-color: white;
    opacity: 0.5;
    transition: 0.6s ease;
    border-radius: 0 3px 3px 0;
}
.next{
    right: 6%;
    border-radius: 3px 0 0 3px;
}


.prev:hover, .next:hover{
    background-color: rgba(0,0,0,7);
}
.text{
    color:chartreuse;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top:0;
}
.dot div{
    cursor: pointer;
    height: 13px;
    width: 13px;
    margin: 0  2px;
    background-color: #ff3f48;
    border-radius: 50%;
    position: relative;
    display: inline-block;
    text-align: center;
    transition: background-color 0.6s ease;
}
.active, .dot:hover{
    background-color: #717173;
}
.slide img{
    width: 96%;
}
.slide{
    -webkit-animation-name:slide ;
    -webkit-animation-duration: 1.5s;
    animation-name:slide;
    animation-duration:1.5s;
}
@keyframes fade {
    from{opacity:4}
    to{opacity:1}
}






